<template>
    <div>
        <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
            <el-breadcrumb-item></el-breadcrumb-item>
        </el-breadcrumb>
        <el-card class="box-card">
            <div style="margin-bottom: 13px">
                <el-button type="primary" size="small" @click="toggleSelection()"></el-button>
                <el-button type="success" size="small" @click="allToChangeState()"></el-button>
                <el-button type="danger" size="small" @click="allToDelete()"></el-button>
                <span style="float: right">

        <template>
            <font size="2px"></font>
            <el-date-picker
                    @change="getTime()"
                    v-model="time"
                    type="daterange"
                    size="small"
                    value-format="yyyy-MM-dd"
                    range-separator=""
                    start-placeholder=""
                    end-placeholder="">
            </el-date-picker>
            &nbsp;
            &nbsp;
            <font size="2px"></font>
            <el-select v-model="choosestate" placeholder="" size="small" style="width: 150px" @change="getall()">
              <el-option
                      v-for="item in options"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
              </el-option>
            </el-select>
        </template>

    </span>
            </div>

            <template>
                <el-table
                        ref="multipleTable"
                        tooltip-effect="dark"
                        :data="tableData"
                        style="width: 100%"
                        border
                        stripe
                        @selection-change="handleSelectionChange">
                    <el-table-column
                            type="selection"
                            width="55">
                    </el-table-column>
                    <el-table-column
                            prop="createTime"
                            label=""
                            width="180">
                        <template slot-scope="scope">
                            {{scope.row.createTime | dateFormatFull}}
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="name"
                            label="">
                    </el-table-column>
                    <el-table-column
                            prop="phone"
                            label="">
                    </el-table-column>
                    <el-table-column
                            prop="address"
                            label="">
                    </el-table-column>
                    <el-table-column
                            prop="text"
                            label="">
                    </el-table-column>
                    <el-table-column
                            prop="state"
                            label=""
                            width="120">
                        <template slot-scope="scope">
                            <span>{{scope.row.state==2?"":scope.row.state==1?"":""}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                            prop="updateperson"
                            label=""
                            width="120">
                    </el-table-column>
                    <el-table-column
                            prop="updatetime"
                            label=""
                            width="180">
                    </el-table-column>
                    <el-table-column
                            fixed="right"
                            label=""
                            align="center"
                            width="230">
                        <template slot-scope="scope">
                            <el-button type="primary" icon="el-icon-chat-line-round" size="mini"
                                       @click="lookDetails(scope.$index,scope.row)">
                            </el-button>
                            <el-button type="danger" icon="el-icon-delete" size="mini"
                                       @click="deleteJoinLeaveMsg(scope.$index,scope.row)">
                            </el-button>
                        </template>
                    </el-table-column>
                </el-table>

            </template>

            <div style="margin-top: 20px">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page="currentPage"
                        :page-sizes="[10, 5, 8, 15]"
                        :page-size="pageSize"
                        layout="total, sizes, prev, pager, next, jumper"
                        :total="totalCount">
                </el-pagination>
            </div>

            <!---->
            <el-dialog title="" custom-class="detailscss" :visible.sync="lookdetailsV" append-to-body>
                <el-form :model="joinLeaveMsg" label-width="70px">
                    <el-form-item label="">
                        <el-input v-model="joinLeaveMsg.name"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="joinLeaveMsg.phone"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="joinLeaveMsg.address"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="joinLeaveMsg.text"></el-input>
                    </el-form-item>
                </el-form>
                <!--<span>{{this.joinLeaveMsg}}</span>-->
                <div slot="footer" class="dialog-footer">
                    <el-button type="primary" size="small" @click="lookdetailsV = false"> </el-button>
                </div>
            </el-dialog>

        </el-card>
    </div>

</template>

<script>
    export default {
        name: "ContactUsMessage",
        data: function () {
            return {
                totalCount: 0,
                pageSize: 10,
                currentPage: 1,
                time: '', //
                options: [{
                    value: '',
                    label: ''
                }, {
                    value: 0,
                    label: ''
                }, {
                    value: 1,
                    label: ''
                }, {
                    value: 2,
                    label: ''
                }],
                choosestate: '', //
                joinLeaveMsg: {},//
                lookdetailsV: false,// 
                tableData: [], //
                multipleSelection: []
            }
        },
        created() {
            this.getAll();
        },
        methods: {
            //
            allToChangeState() {
                this.$confirm('', '', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '',
                    cancelButtonText: ''
                })
                    .then(() => {
                        const edit = {
                            ids: this.multipleSelection
                        };
                        this.axios.post("letterMessage/changeLetterMessageStateByIds", edit)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                            })
                    })
            },
            //
            allToDelete() {
                this.$confirm('', '', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '',
                    cancelButtonText: ''
                })
                    .then(() => {
                        const edit = {
                            ids: this.multipleSelection
                        };
                        this.axios.post("letterMessage/deleteLetterMessageByIds", edit)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                            })
                    })
            },
            getTime() {
                if (this.time == null) {
                    this.time = '';
                }
                this.getAll();
            },
            //
            lookDetails(index, row) {
                this.joinLeaveMsg = {};
                this.joinLeaveMsg = row;
                this.changeState(index, row);
                this.lookdetailsV = true;
            },
            //
            changeState(index, row) {
                const edit = {
                    ids: row.id
                };
                this.axios.post("letterMessage/changeLetterMessageStateByIds", edit)
                    .then(response => {
                        if (response.data.status == 200) {
                        } else {
                            this.$message.error(response.data.msg);
                        }
                        this.getAll();
                    })
            },
            //
            deleteJoinLeaveMsg(index, row) {
                this.$confirm('', '', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '',
                    cancelButtonText: ''
                })
                    .then(() => {
                        const edit = {
                            ids: row.id
                        };
                        this.axios.post("letterMessage/deleteLetterMessageByIds", edit)
                            .then(response => {
                                if (response.data.status == 200) {
                                    this.$message.success(response.data.msg);
                                } else {
                                    this.$message.error(response.data.msg);
                                }
                                this.getAll();
                            })
                    })
            },
            getAll() {
                this.axios.get("letterMessage/getLetterMessageList", {
                    params: {
                        "page": this.currentPage,
                        "size": this.pageSize,
                        "state": this.choosestate,
                        "startTime": this.time[0],
                        "endTime": this.time[1],
                    }
                })
                    .then(response => {
                        console.log(response);
                        this.tableData = response.data.data.records;
                        this.totalCount = response.data.data.total;
                    })

            },
            toggleSelection(rows) {
                if (rows) {
                    rows.forEach(row => {
                        this.$refs.multipleTable.toggleRowSelection(row);
                    });
                } else {
                    this.$refs.multipleTable.clearSelection();
                }
            },
            handleSelectionChange(val) {
                let arr = [];
                val.forEach((item) => {
                    arr.push(item.id)
                });
                this.multipleSelection = arr;
            },
            handleSizeChange(val) {
                this.pageSize = val;
                this.getAll();
            },
            handleCurrentChange(val) {
                this.currentPage = val;
                this.getAll();
            },
        },
    }
</script>

<style scoped>

</style>
