import '@babel/polyfill'
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

/**
 * 
 */
import './permission/permission'
/**
 * 
 */
import dataV from '@jiaminghi/data-view'

Vue.use(dataV)
/**
 * antv/g2
 */
import * as G2 from '@antv/g2';
/**
 * axios
 */
import axios from 'axios'
import qs from 'qs'
/**
 * bootstrapjs  cssEntry3.3.7
 */
/*import "bootstrap/dist/css/bootstrap.css"*/
/*import "bootstrap/dist/js/bootstrap.min.js"*/
/**
 * WOW
 */
import 'animate.css'
/**
 * element
 */
import './plugins/element.js'
/**
 * antdesign 
 */
/*import '@/plugins/antdesign.js'*/
/**
 * 
 */
import '@/css/components/global.css'

/**
 * moment
 */
import moment from 'moment'
import {Loading, Message, MessageBox, Notification} from "element-ui";

/**
 * axiosanimate
 * axios
 */
/*axios.defaults.baseURL = '/api';*/
axios.defaults.baseURL = 'http://139.224.187.103:2223/';
axios.defaults.timeout = 15000; //
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'; // 
//axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8'; // 
axios.defaults.withCredentials = true;
axios.interceptors.request.use(config => {
    console.log(config.data)
    config.headers.Authorization = window.sessionStorage.getItem('token');
    config.data = qs.stringify(config.data);
    console.log(config.data)
    return config
});
axios.interceptors.response.use(response => {
    console.log(response.data)
    if (response.data.status == 401) {
        router.push({path: '/login'})
    }
    return response;
});

/**
 * axios
 */
Vue.prototype.axios = axios
Vue.prototype.qs = qs
Vue.prototype.moment = moment

/**
 * element
 */
Vue.prototype.$loading = Loading.service;
Vue.prototype.$msgbox = MessageBox;
Vue.prototype.$alert = MessageBox.alert;
Vue.prototype.$confirm = MessageBox.confirm;
Vue.prototype.$prompt = MessageBox.prompt;
Vue.prototype.$notify = Notification;
Vue.prototype.$message = Message;

/**
 * 
 */
Vue.filter('dateFormat', function (daraStr) {
    if (!daraStr) return ''
    return moment(daraStr).format('YYYY-MM-DD')
})
Vue.filter('dateFormatFull', function (daraStr) {
    if (!daraStr) return ''
    return moment(daraStr).format('YYYY-MM-DD HH:mm:ss')
})


/**
 * 
 */
Vue.config.productionTip = true

new Vue({
    router,
    store,
    render: h => h(App)
}).$mount('#app')


